import React from 'react';
import Admission from "./components/admission";
import Nav from './components/nav';
import Testimony from "./components/testimony";
import question from "../images/question.png";

const Home = () => {
    return (
        <>
            <Nav />
            <Header />
            <Whoweare />
            <Admission />
            <Testimony />
        </>
    );
}
const Header = () => {
    return (
        <section className="section hero">
            <div className="header">
                <h1>OUR MISSION</h1>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Tempore consequatur aut iure pariatur beatae ipsa quasi sunt! Excepturi, voluptates! Ducimus?</p>
            </div>
            <div className="nav-links">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </section>
    );
}

const Whoweare = () => {
    return (
        <section class="who-we-are section">
            <img src={question} alt="question" className="question-mark" />
            <h1>WHO ARE WE?</h1>
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Fuga odit reiciendis, debitis hic aliquam adipisci minus atque consectetur, alias maiores architecto amet ducimus. Sunt, fugit quasi nesciunt quam corrupticlass.</p>
        </section>
    );
}

export default Home;