import React from 'react';
import parent1 from "../../images/testimony/001.png"
const Testimony = () => {
    return (
        <section className="section testimony">
            <h1>HEAR WHAT OUR PARENTS HAVE TO SAY</h1>
            <div className="testimonies">
                <img src={parent1} alt="parent img" className="testifier-img" />
                <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Dolores explicabo deserunt facere expedita aspernatur alias, quisquam voluptatum ipsa quam cumque delectus culpa aut ea libero error sunt, iste aliquid harum.</p>
                <p className="testifier-name">- Lorem Ipsum</p>
            </div>
            <div className="nav-links">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </section>
    );
}

export default Testimony;