import React from 'react';
import menu from "../../images/menu.svg";
import home from "../../images/nav/home.svg";
import question from "../../images/nav/question.svg";
import phone from "../../images/nav/phone.svg";
import signin from "../../images/nav/sign-in.svg";
const Nav = () => {
    return (
        <>
            <nav>
                <span style={{ display: "none" }} className="logo">LOGO</span>
                <img style={{ display: "none" }} src={menu} alt="ham-burger-menu" />



                <div className="bottom-link"><img src={home} alt="home" />home</div>
                <div className="bottom-link"><img src={phone} alt="phone" />contact us</div>
                <div className="bottom-link"><img src={home} alt="home" />gallery</div>
                <div className="bottom-link"><img src={question} alt="question" />faq</div>
                <div className="bottom-link"><img src={signin} alt="signin" />login</div>


            </nav>
            <div className="hide" style={{ display: "none" }}>
                <ul>
                    <li><img src={home} alt="home" />home</li>
                    <li><img src={phone} alt="phone" />contact us</li>
                    <li><img src={home} alt="home" />gallery</li>
                    <li><img src={question} alt="question" />faq</li>
                    <li><img src={signin} alt="signin" />login</li>
                </ul>
                <span className="close">x</span>
            </div>
        </>
    );
}

export default Nav;